import common from '@/libs/common.mixin'
import FileItem from 'c%/FileItem.vue'
import AddPopup from './artwork/add.vue'
import SelectClass from 'c%/SelectClass.vue'	
export default{
	mixins: [ common ],
	components: {
		AddPopup,
		FileItem,
		SelectClass
	},
	data () {
		return {
			data: [],
			search_field: {
				cid: 0
			}
		}
	},
	watch: {
		'search_field.cid': function (val) {
			this.downCallback()
		}
	},
	methods: {
		showPopup () {
			this.$refs.add.show()
		},
		detail (item) {
			this.$router.push({path:'./artwork/'+item.sart_id,params:{id:item.sart_id}})
		},
		formatDate (time) {
			time = time||''
			let result = time.substring(5,10)
			return result
		},
		hook_get_param (params) {
			if(this.search_field.cid > 0){
				params.cid = this.search_field.cid 					
			}
		}
	},
	computed: {
		data_uri () {
			return 'student_artworks?with=student_artwork_attachment,student&eid='+this.eid$
		},
		style () {
			let top = this.headerHeight + 46
			return `top:${top}px;height:calc(100% - ${top}px);position:fixed`
		}
	}
}



